.quotecontact {
    background: #5F5138;
    width: 100%;
    padding: 80px 0 115px;

    &__desc{
        font-family: 'Roboto';
        font-style: normal;
        font-weight: 400;
        font-size: 16px;
        line-height: 26px;
        display: flex;
        align-items: center;
        text-align: center;
        color: var(--white);
        margin-top: 25px;
    }
    &__link {
       color:var(--f);
       margin-right: 5px;
       margin-left: 5px;
    }
}