.header {
    position: absolute;
    height: 83px;
    top: 0;
    background: rgba(84, 83, 82, 0.63);
    border: 1px solid rgba(255, 255, 255, 0.2);
    width: 100%;
  

    &__link:not(:first-child) {
        margin-left: 50px;
    }

    &__link {
        position: relative;
        font-family: 'Nunito';
        font-style: normal;
        font-weight: 600;
        font-size: 16px;
        line-height: 22px;
        color: var(--white);
        flex: none;
        order: 1;
        flex-grow: 0;
        transition: 0.3s;
        &.canHover::after{
            position: absolute;
            content: "";
            bottom: -5px;
            left: 50%;
            height: 2px;
            background-color: var(--f);
            width: 0%;
            transition: 0.3s;
            transform: translateX(-50%);
        }
        &.canHover:hover{
            color: var(--f);
        }
        &.canHover:hover::after {
            width: 110%;
        }
    }

    &__button {
        border: 1px solid var(--d);
        border-radius: 100px;
        font-family: 'Roboto';
        font-style: normal;
        font-weight: 400;
        font-size: 16px;
        line-height: 19px;
        color: var(--white);
        width: 209px;
        height: 40px;
        text-align: center;
        cursor: pointer;

     
    }
}